  position: relative;
  .queryBox {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    text-align: right;
  }
  .content_box {
    margin: 0 15px 0;
    .page_box{
      width: 100%;
      padding: 15px 0;
      text-align: right
    }
  }
  .btn-exportBtn {
    background: #2d579b;
    border-color: #2d579b;
    &:hover {
      background: #1a3a6f;
      border-color: #1a3a6f;
    }
  }
}
